import React, { useContext } from "react";
import { useNavigate } from "react-router-dom";
import "./styles/CheckBuy.css";
import HeaderBasic from "./HeaderBasic";
import GiftItem from "./GiftItem";
import BottomBar from "./BottomBar";
import { UserPurchaseContext } from "./UserPurchaseContext";
function CheckBuy() {
  const navigate = useNavigate();

  // const location = useLocation();
  // const data = location.state?.data; //   
  // console.log("location.state.data:", data); //   

  const { purchases } = useContext(UserPurchaseContext); // UserPurchaseContext purchases 

  return (
    <>
      <HeaderBasic />
      <div className="screen">
        <div className="buy-history-title">
          <div className="buy-history-container">
            <div className="buy-history-text"> </div>
          </div>
        </div>
        {purchases.map((purchase, index) => (
          <GiftItem
            key={index}
            item={purchase.item}
            imgpath={purchase.imgpath}
            onClick={() => navigate(`/checkbuy/detail/${index}`)} //   
          />
        ))}
      </div>
      <BottomBar />
    </>
  );
}

export default CheckBuy;
