import React, { useContext } from "react";
import { useLocation } from "react-router-dom";
import ProductList from "./component/ProductList";
import Points from "./component/Points";
import "./styles/Shop.css";
import GiftItem from "./component/GiftItem";
import BottomBar from "./BottomBar";
import HeaderShop from "./HeaderShop";
import { PointsContext } from "./PointsContext"; // PointsContext import

const Shop = () => {
    const location = useLocation();
    const { data } = location.state || { data: { userpoint: 0, object: [] } }; //  
    const { points, setPoints } = useContext(PointsContext); // PointsContext points setPoints 

    //    points 
    React.useEffect(() => {
        if (data.userpoint) {
            setPoints(data.userpoint);
        }
    }, [data.userpoint, setPoints]);

    return (
        <>
            <HeaderShop />
            <div className="screen">
                <div className="points-display-box">
                    <div className="points-display-container">
                        <div className="points-display-text"> : {points}P</div>
                    </div>
                </div>

                <GiftItem />
            </div>

            <BottomBar />
        </>
    );
};

export default Shop;
