import React from "react";
import "./styles/CheckPoint.css";
import { useLocation } from "react-router-dom";
import HeaderBasic from "./HeaderBasic";
import HistoryItem from "./HistoryItem";
import BottomBar from "./BottomBar";

function CheckPoint() {
  const location = useLocation();
  const data = location.state?.data; //   

  console.log("location.state.data:", data); //   

  //   historyData ,     
  const historyData = data?.object?.map(transaction => ({
    title: transaction.content,
    date: new Date(transaction.time).toLocaleString(), //     
    description: transaction.content,
    points: `${transaction.point > 0 ? '+' : ''}${transaction.point}P`,
    status: transaction.point > 0 ? "" : "",
    isPositive: transaction.point > 0
  })) || [];

  return (
      <>
        <HeaderBasic />
        <div className="screen">
          <div className="point-history-title">
            <div className="point-history-container">
              <div className="point-history-text"> </div>
            </div>
          </div>
          <div className="points-history">
            {historyData.length > 0 ? (
                historyData.map((item, index) => (
                    <HistoryItem
                        key={index}
                        title={item.title}
                        date={item.date}
                        description={item.description}
                        points={item.points}
                        status={item.status}
                        isPositive={item.isPositive}
                    />
                ))
            ) : (
                <div className="no-history">  .</div>
            )}
          </div>
          <div className="points-banner">
            <div className="banner-text"> : {data?.userpoint || 0}P</div>
          </div>
        </div>
        <BottomBar />
      </>
  );
}

export default CheckPoint;
